<template>
  <div>
    <input type="text" v-bind:value="message" />
  </div>
</template>
<script>
export default {
  data() {
    return {
      message: "Vue.js를 배우는건 좋은 선택입니다.",
    };
  },
  methods: {
    reverseMessage: function () {
      this.message = this.message.split("").reverse().join("");
    },
  },
};
</script>
